import React, { useState } from 'react'
import { ethers } from 'ethers'
import { contractAddress, abi } from '../constants'

export default function Donations(props) {

  const colorStyle = {
    color: `${props.theme==='light'?'black':'#e5e5e1'}`
  }

  const bgStyle = {
    backgroundColor: `${props.theme==='light'?'white':'#414141'}`,
    boxShadow: `${props.theme==='light'?'0px 0px 8px black':'0px 0px 8px white'}`
  }

  const inputStyle = {
    color: `${props.theme==='light'?'black':'#e5e5e1'}`,
    backgroundColor: `${props.theme==='light'?'white':'#414141'}`
  }

  const [donationMessage, setDonationMessage] = useState('');
  
  const commitDonation = async() => {
    const donationInput = document.getElementById("donationAmt");
    let donationAmt = donationInput.value;

    setDonationMessage('Processing...');
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const lottery = new ethers.Contract(contractAddress, abi, signer);
    const amt = ethers.utils.parseEther(donationAmt);
    const response = await lottery.donate({value: amt});
    
    setDonationMessage('Thank You!, Please wait while we fetch your transaction hash.');
    
    const receipt = await response.wait(1);
    
    setDonationMessage(`Thanks! Your txn hash is - ${receipt.transactionHash}`);

    props.showAlert("Success!", "Donations Received");

    donationInput.value="";
}

  return (
    <>
        <div id='donationComponent'>
            <h2 style={colorStyle}>
                Make a Donation
            </h2>
            <h2 style={colorStyle}>
                Make a Difference
            </h2>
            <div style={bgStyle} className='don-content'>
                <div>
                <p style={colorStyle}>
                    Meow Meow Organization has started an initiative to provide free food and resources to the needy people and fulfill their basic necessities for their survival.
                    You can make a difference in the lives of the needy people by showing a little support.
                    Your will also be rewarded with NFT when you donate.
                </p>
                <p style={colorStyle}>Enter or choose a Amount</p>
                </div>
                <div>
                    <fieldset className='priceInput'>
                    <legend>
                        <label style={colorStyle} htmlFor="donationAmt" className='label'>Enter Amt(in ETH): </label>
                    </legend>
                    <input style={inputStyle} type="number" id='donationAmt' />
                    </fieldset>
                    <button onClick={commitDonation} className='buttonCSS' id='makeDonation'>Make Donation</button>
                    <p style={colorStyle}>{donationMessage}</p>
                </div>
            </div>
        </div>
    </>
  )
}
