import React from 'react'

export default function Alert(props) {

  const dismissAlert = () => {
    props.showAlert('','');
  }

  return (
    props.alertMessage && (<div id='alert'>
        <div><strong>{props.alertMessage.title}</strong></div>
        &nbsp;
        <div><span>{props.alertMessage.message}</span></div>
        <div><span className='crossSign' onClick={dismissAlert}>&times;</span></div>
    </div>
    )
  )
}
