import React, { useState } from 'react'
import metamask from '../Images/metamask.jpg'
import etherImage from '../Images/ether.webp'
import { ethers } from 'ethers'
import { contractAddress, abi } from '../constants'

export default function Lottery(props) {
  const cardsStyle = {
    backgroundColor: `${props.theme==='light'?'white':'#414141'}`,
    boxShadow: `${props.theme==='light'?'0px 0px 8px black':'0px 0px 8px white'}`,
    color: `${props.theme==='light'?'black':'white'}`
  }  

  const headColor = {
    color: `black`
  }

  const buttonStyle = {
    // backgroundColor: `${props.theme==='light'?'#e5e1e1':'transparent'}`,
    // color: `${props.theme==='light'?'black':'white'}`,
    // boxShadow: `${props.theme==='light'?'0px 0px 7px black':'0px 0px 7px white'}`
  }

  const [connectResponse, setConnectResponse] = useState('Click on the button to Connect');

  const connect = async() => {
    if (typeof window.ethereum !== 'undefined') {
        setConnectResponse('Connecting...');

        try {
            await window.ethereum.request({method: 'eth_requestAccounts'});
            setConnectResponse('Connected!');
            props.showAlert('Success!', 'Connected to Metamask');
        }
        catch(err) {
            if (err.message.toLowerCase().includes('user rejected')) {
                setConnectResponse('Transaction was Aborted!');
                props.showAlert('Error!', 'Transaction Aborted');
              }
              else {
                setConnectResponse('Uh-oh! Caught an Error');
                props.showAlert('Error!', 'Transaction Aborted');
            }
        }
    }
  }

  const [enterLotteryResponse, setEnterLotteryResponse] = useState('Lottery Awaits!');

  const enterLottery = async() => {
    setEnterLotteryResponse('Processing...');
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const lottery = new ethers.Contract(contractAddress, abi, signer);
    const entranceFess = await lottery.getLotteryFees();
    
    await new Promise(async(resolve, reject) => {
      let receipt;
      lottery.on("participantEntered", () => {
        props.showAlert('Success!', 'Your Entry was Received');
        setEnterLotteryResponse(`Your Transaction hash is: ${receipt.transactionHash}`);
        resolve();
      })
      
      try {
        const response = await lottery.enterLottery({value: entranceFess});
        setEnterLotteryResponse('Please wait while we fetch your txn hash');
        receipt = await response.wait(1);
      }
      catch(err) {
        setEnterLotteryResponse('Transaction was Rejected!');
        props.showAlert('Error!', 'Transaction Aborted');
        reject(err);
      }
    })
  }


  return (
    <>
        <div id='lottery-section'>
            <div className='content' style={cardsStyle}>
                <div className='contents highlight' style={headColor}><h2>Connect to Wallet</h2></div>
                <div className='contents'><img src={metamask} alt="meow" /></div>
                <div className='contents'><p>Decentralized Web Awaits</p></div>
                <div className='contents'><p>What are you waiting for, Connect your wallet now!</p></div>
                <div className='contents'><button className='buttonCSS' style={buttonStyle} onClick={connect}>Connect</button></div>
                <div className='contents'><p className='response'>{connectResponse}</p></div>
            </div>
            <div className='content' style={cardsStyle}>
                <div className='contents highlight' style={headColor}><h2>Enter Lottery</h2></div>
                <div className='contents'><img src={etherImage} alt="participate" /></div>
                <div className='contents'><p>Feeling Lucky</p></div>
                <div className='contents'><p>Enter into the Lottery and win bunch of ETH</p></div>
                <div className='contents'><button className='buttonCSS' style={buttonStyle} onClick={enterLottery}>Enter Lottery</button></div>
                <div className='contents'><p className='response'>{enterLotteryResponse}</p></div>
            </div>
        </div>
    </>
  )
}
