import React, { useState } from 'react'
import { ethers } from 'ethers'
import { abi, contractAddress } from '../constants';

export default function Winner(props) {

  const [recentWinner, setRecentWinner] = useState('--No Winner Selected--'); 
  
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const signer = provider.getSigner();
  const lottery = new ethers.Contract(contractAddress, abi, signer);
  
  lottery.on("winnerSelected", async() => {
    const ourWinner = await lottery.getRecentWinner();
    setRecentWinner(ourWinner);
  })

    const currTheme = {
        backgroundColor: `${props.theme==='light'?'white':'#414141'}`,
        color: `${props.theme==='light'?'black':'#e5e5e1'}`,
        boxShadow: `${props.theme==='light'?'0px 0px 8px black':'0px 0px 8px white'}`
    }

  return (
    <div id='winner' style={currTheme}>
      <h2>Congratulations to the Winner</h2>  
      <h2>Our Recent Winner is:</h2>
      <h3>{recentWinner}</h3>
    </div>
  )
}
