import React, { useState } from 'react'
import { Link } from 'react-router-dom';

export default function Navbar(props) {
  const [theme, setTheme] = useState('light');
  const [slideIt, setSlideIt] = useState(null);

  const flipTheme = () => {
    props.changeTheme();
    if (theme === 'light') {
        setTheme('dark');
        setSlideIt({
            transform: 'translateX(28px)'
        }); 
    }
    else {
        setTheme('light');
        setSlideIt(null); 
    }
  }  

  const forBoxShadow = {
    boxShadow: `${theme==='light'?'0px 0px 8px black':'none'}`
  }

  const [acctAddress, setAcctAddress] = useState('Connect your Wallet');
  window.ethereum.on("accountsChanged", () => {
    setAcctAddress(window.ethereum.selectedAddress?window.ethereum.selectedAddress:'Connect your Wallet');
  })
  window.onload = () => {
    setAcctAddress(window.ethereum.selectedAddress?window.ethereum.selectedAddress:'Connect your Wallet');
  }


  return (
    <nav style={forBoxShadow}>
        <div className='leftPane'>
            <ul id='leftLinks'>
                <li className='heading'><Link to="/">Decentralized Lottery</Link></li>
                <li><Link to="">About Us</Link></li>
                <li><Link to="/donate">Donate to Needy</Link></li>
                <li><Link to="">Contact Us</Link></li>
            </ul>
        </div>

        <div className='rightPane'>
            <div className='content'>
                <div id='acctAddress'>{acctAddress}</div>
            </div>
            <div className='content slideSection'>
                <div className='slider' onClick={flipTheme}>
                    <div style={slideIt} className='roller'></div>
                </div>
                <div>
                    Dark Theme
                </div>
            </div>
        </div>
    </nav>
  )
}
