import { useState } from 'react';
import './App.css';
import Navbar from './components/Navbar';
import Lottery from './components/Lottery';
import Winner from './components/Winner';
import Alert from './components/Alert';
import Donations from './components/Donations';

import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link
} from "react-router-dom";

function App() {
  // writing function
  const [theme, setTheme] = useState('light');

  document.body.style.backgroundColor = `${theme === 'light'?'white':'#616161'}`;
  const changeTheme = () => {
    if (theme === 'light') {
      setTheme('dark');
    }
    else {
      setTheme('light');
    }
  }


  // ALert Section
  const [alertMessage, setAlertMessage] = useState(null);

  const showAlert = (title, message) => {
    if (title==='' && message==='') {
      setAlertMessage(null);
    }
    else {
      setAlertMessage({
        title: title,
        message: message
      })
    }
  }


  // jsx segment
  return (
    <Router>
    <>
      <Navbar  changeTheme={changeTheme} />
      <Alert alertMessage={alertMessage} showAlert={showAlert}/>
    

      <Routes>
          <Route exact path='/' element={<><Lottery theme={theme} showAlert={showAlert}/> <Winner theme={theme}/> </>} />
          <Route exact path='/donate' element={<Donations theme={theme} showAlert={showAlert}/>} />
        </Routes>
    </>
    </Router>
  );
}

export default App;
